@font-face{
  font-family: 'custom-font';
  src: url('/Font/main.otf');
}
body{
    background: url('./background_blur.jpeg');
    background-repeat: repeat;
    height:100%;
    width:100%;
}

h4{
    text-align: center;
    padding-bottom: 20px;
}
.about-text{
    font-size:18px;
}
.about-text-final{
    font-size: 22px;
    text-align: center;
}
.Main-text{
    text-align: center;
    padding-top:200px;
    animation:typewriter 3s;
    overflow:hidden;
    font-size: 20px;

}
p{
  font-family: custom-font;

}
.links{
    padding-top:120px;
}
a{
    color:black;
    text-decoration: none;
    font-size: 30px;
    text-shadow: 2px 4px 3px rgba(0,0,0,0.3);
    display: block;
    margin-left: auto;
    text-align: center;
    margin-right: auto;
}

.shop-image{
    height:500px;
}
.about-pic{
    height:350px;
    width:auto;
    padding-top:40px;
}
.lookbook-image{
    height:auto;
    width:auto;
    max-height: 500px;
    max-width: 380px;
    border: solid;
    border-color:white;
    border-radius: 40px 40px 40px 40px;
}
.carrot{
    font-size: 50px;
}
.arrow{
    height:30px;
    width:auto;
    mix-blend-mode: multiply;

}
.shop-div{
    border:solid;
}



